.top-picks-row {
  margin: 20px 0;
  padding: 10px 20px;
  position: relative;
}

.row-title {
  font-size: 2rem;
  color: #fff;
  margin-bottom: 20px;
  animation: fadeIn 1s ease-in-out;
}

.card-row {
  display: flex;
  gap: 15px;
  overflow-x: auto;
  padding: 10px 0;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
  position: relative;
  padding: 10px 40px; /* Add padding for the buttons */
  margin: 0 -20px; /* Compensate for parent padding */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.card-row::-webkit-scrollbar {
  display: none;
}

.card-row-content {
  display: flex;
  gap: 15px;
  overflow-x: auto;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
  padding: 10px 0;
  position: relative;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.card-row-content::-webkit-scrollbar {
  display: none;
}

.card-row::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.card-row::-webkit-scrollbar-thumb {
  background: rgba(255, 0, 0, 0.5);
  border-radius: 4px;
}

.card-row::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 0, 0, 0.7);
}

.pick-card {
  position: relative;
  width: 250px;
  height: 200px;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  animation: slideIn 0.5s ease forwards;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.pick-card:hover {
  transform: scale(1.05);
  cursor: pointer;
  box-shadow: 0 8px 20px rgba(255, 0, 0, 0.5); /* Netflix red glow on hover */
}

.pick-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.pick-card:hover .pick-image {
  transform: scale(1.1);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.pick-card:hover .overlay {
  opacity: 1;
}

.pick-label {
  color: #fff;
  font-size: 1.2rem;
  font-weight: bold;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.pick-label:hover {
  color: rgb(153, 80, 80);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.scroll-button {
  position: absolute;
  top: 0;
  height: 100%;
  width: 60px;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0.3) 70%,
    rgba(0, 0, 0, 0) 100%
  );
  border: none;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  transition: all 0.3s ease;
  pointer-events: auto;
}

.scroll-button.right {
  right: 0;
  background: linear-gradient(
    to left,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0.3) 70%,
    rgba(0, 0, 0, 0) 100%
  );
}

.scroll-button::before {
  content: "";
  width: 15px;
  height: 15px;
  border-top: 3px solid white;
  border-right: 3px solid white;
  display: block;
  transition: all 0.3s ease;
}

.scroll-button:hover::before {
  border-color: rgba(255, 0, 0, 0.8);
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.5);
}

.scroll-button.left::before {
  transform: rotate(-135deg);
  margin-left: 5px;
}

.scroll-button.right::before {
  transform: rotate(45deg);
  margin-right: 5px;
}
