.courses-container {
  padding: 40px;
  text-align: center;
  color: #fff;
  max-width: 1000px;
  margin: auto;
}

.courses-title {
  font-size: 2.5rem;
  color: #e50914; /* Netflix red */
  margin-bottom: 30px;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(4, 200px);
  gap: 80px;
  margin-top: 20px;
  justify-content: start;
}

.course-card {
  background-color: #1e1e1e;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  color: #e6e6e6;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 200px;
  margin: 0;
}

.course-card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(255, 0, 0, 0.5); /* Netflix red glow on hover */
}

.course-code {
  font-size: 1.7rem;
  font-weight: bold;
  color: #fff;
  margin-top: 15px;
  margin-bottom: 5px;
  padding: 5px 10px;
  border-radius: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: relative;
}

.course-code::after {
  content: "";
  position: absolute;
  bottom: 1px;
  left: 0;
  width: 100%;
  border-bottom: 2px solid #e50914;
}

.course-name {
  display: flex;
  justify-content: center;
  font-size: 1.1rem;
  margin: 10px 0;
}

.course-name h3 {
  margin: 0;
  color: #fff;
}

.course-description {
  font-size: 0.9rem;
  color: #999;
  margin: 5px 0;
}

.course-place {
  font-size: 0.8rem;
  color: #999;
  margin: -7px 0;
}
.course-placetaken {
  font-size: 0.8rem;
  color: #999;
  display: inline-block;
  vertical-align: middle;
}

/* Popup Styles */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

.popup-card {
  background-color: #1e1e1e;
  border-radius: 10px;
  padding: 30px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 8px 32px rgba(255, 0, 0, 0.3);
  animation: slideIn 0.3s ease;
}

.close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  color: #e50914;
  font-size: 2rem;
  cursor: pointer;
  padding: 5px;
  line-height: 1;
  transition: transform 0.2s ease;
}

.close-button:hover {
  transform: scale(1.2);
}

.popup-content {
  color: #fff;
}

.popup-header {
  text-align: center;
  margin-bottom: 20px;
}

.popup-header .course-code {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: 15px;
  margin-bottom: 5px;
  color: #fff;
  padding-bottom: 5px;
  position: relative;
}

.popup-header .course-code::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  border-bottom: 2px solid #e50914;
}

.popup-header h3 {
  font-size: 1.8rem;
  margin: 0;
  color: #fff;
}

.popup-details {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #333;
}

.popup-details p {
  margin: 0;
  color: #e50914;
  font-weight: bold;
}

.popup-description {
  text-align: left;
}

.popup-description h4 {
  color: #e50914;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.popup-description p {
  color: #ccc;
  line-height: 1.6;
  margin: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.courses-title {
  font-size: 2.5rem;
  color: #e6e6e6; /* Netflix red */
  margin-bottom: 30px;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeSlideIn 1s ease forwards;
}

/* Category Animation */
.category-title {
  font-size: 1.8rem;
  color: #e6e6e6;
  margin: 30px 0 20px;
  border-bottom: 2px solid #e50914;
  padding-bottom: 5px;
  display: inline-block;
  opacity: 0;
  transform: translateY(20px);
  animation: fadeSlideIn 0.8s ease forwards;
}

.category-title:nth-child(1) {
  animation-delay: 0.2s;
}
.category-title:nth-child(2) {
  animation-delay: 0.4s;
}
.category-title:nth-child(3) {
  animation-delay: 0.6s;
}
.category-title:nth-child(4) {
  animation-delay: 0.8s;
}
.category-title:nth-child(5) {
  animation-delay: 1s;
}

/* Keyframe for Slide and Fade-in Animation */
@keyframes fadeSlideIn {
  to {
    opacity: 1;
    transform: translateY(10);
  }
}
